  justify-content: space-between;
  align-items: flex-end;
  font-size: 16px;
  margin-block-end: 6px;
}
.product-list .product-filter .col label .clear {
  cursor: pointer;
  min-width: 50px;
  text-align: right;
}
.product-list .product-filter .col label .clear:hover {
  color: #FF9800;
}
.product-list .product-filter .col input, .product-list .product-filter .col select {
  width: 100%;
  padding: 15px 13px !important;
  font-size: 16px;
  color: #666666;
  border: 1px solid #000000;
}
.product-list .product-filter .col input::placeholder, .product-list .product-filter .col select::placeholder {
  color: #666666;
}
.product-list .product-filter .col input[type=search], .product-list .product-filter .col select[type=search] {
  padding-inline-start: 48px !important;
}
.product-list__product-result {
  padding-block-end: 88px;
}
@media screen and (max-width: 1199px) {
  .product-list__product-result {
    padding-block-end: 50px;
  }
}
.product-list__product-result__info {
  font-size: 14px;
}
.product-list #ebrands-shop-product-list ul li .woocommerce-loop-product__title {
  padding-block-start: 0;
  padding-block-end: 5px;
  font-weight: 600;
}

.single-product .woocommerce-product-gallery {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 16px;
}
.single-product .woocommerce-product-gallery .flex-viewport {
  order: 2;
  flex: 0 0 calc(100% - 8px - 88px);
  max-width: calc(100% - 8px - 88px);
}
@media screen and (max-width: 575px) {
  .single-product .woocommerce-product-gallery .flex-viewport {
    flex: 0 0 100%;
    max-width: 100%;
  }
}
.single-product .woocommerce-product-gallery .flex-control-nav {
  order: 1;
  flex: 0 0 80px;
  max-width: 80px;
  gap: 16px;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: column;
}
@media screen and (max-width: 575px) {
  .single-product .woocommerce-product-gallery .flex-control-nav {
    display: none;
  }
}
.single-product .woocommerce-product-gallery .flex-control-nav li {
  width: 100% !important;
}
.single-product .woocommerce-product-gallery .flex-control-nav li img {
  border: 1px solid #CCCCCC;
  height: 100px !important;
  object-fit: cover;
}
@media screen and (max-width: 1199px) {
  .single-product .woocommerce-product-gallery .flex-control-nav li img {
    height: 80px !important;
  }
}
.single-product div.product {
  overflow: hidden;
}
.single-product div.product div.summary .product_title {
  font-size: 40px;
  letter-spacing: 0;
  margin-block-end: 8px;
}
.single-product div.product div.summary .price {
  font-size: 24px;
  font-weight: 700;
  margin-block-end: 15px;
}
.single-product div.product div.summary .woocommerce-product-rating {
  margin-block-end: 15px;
}
.single-product div.product div.summary .woocommerce-product-rating .star-rating {
  font-size: 17px;
  margin-inline-end: 15px;
}
.single-product div.product div.summary .woocommerce-product-rating .woocommerce-review-link {
  font-size: 14px;
}
.single-product div.product div.summary .woocommerce-product-details__short-description {
  font-size: 16px;
  letter-spacing: 0.25px;
}
.single-product div.product div.summary .custom-varient-wrap {
  margin-block-end: 21px;
}
.single-product div.product div.summary .custom-varient-wrap select {
  padding: 14px 12px;
}
.single-product div.product div.summary .product_meta .posted_in {
  margin-block-end: 25px;
  display: block;
}
.single-product div.product div.summary .product_meta .posted_in a {
  padding: 8px 16px;
  margin-inline-end: 6px;
  border: 1px solid #000000;
  background: transparent;
  color: #000000;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  transition: all 0.4s;
}
.single-product div.product div.summary .product_meta .posted_in a.active, .single-product div.product div.summary .product_meta .posted_in a:first-of-type {
  background: #000000;
  color: #ffffff;
}
.single-product div.product div.summary .product_meta .posted_in a.disabled, .single-product div.product div.summary .product_meta .posted_in a:last-of-type {
  opacity: 0.25;
}
.single-product div.product div.summary .product_meta .posted_in a:hover {
  background-color: #393939;
  color: #ffffff;
}
.single-product div.product div.summary .quantity .qty {
  width: 66px;
  height: 50px;
  padding-inline-start: 4px;
}
.single-product div.product div.summary form.cart {
  margin-block-end: 14px;
}
.single-product div.product div.summary .single_add_to_cart_button {
  clear: left;
  margin-block-start: 30px;
  width: 100%;
  padding: 16px 15px;
}
.single-product div.product div.summary .single_add_to_cart_button.disabled {
  background-color: #FF9800;
  color: #000000;
}
.single-product div.product div.summary .label {
  display: block;
  margin-bottom: 5px;
  font-size: 16px;
}
.single-product div.product div.summary .info-disc {
  font-size: 12px;
  padding-block-start: 15px;
  margin-block-end: 0;
}
.single-product div.product div.summary .content__accordion {
  padding-block-start: 32px;
}
.single-product div.product div.summary .content__accordion .content__accordion_item .content__accordion_head {
  padding: 19px 40px 17px 0;
}
.single-product div.product div.summary .content__accordion .content__accordion_item .content__accordion_body_inner {
  font-size: 16px;
}
.single-product div.product div.summary .variations_form table.variations tr {
  display: block;
}
.single-product div.product div.summary .variations_form table.variations tr .value,
.single-product div.product div.summary .variations_form table.variations tr .label {
  display: block;
  text-align: left;
  font-size: 16px;
  font-weight: 400;
  margin-block-end: 0;
}
.single-product div.product div.summary .variations_form table.variations tr .value label,
.single-product div.product div.summary .variations_form table.variations tr .label label {
  font-weight: 400;
}
.single-product div.product div.summary .variations_form table.variations tr:not(:last-of-type) .value {
  margin-bottom: 15px;
}
.single-product div.product div.summary .variations_form table.variations select {
  height: 48px;
}
.single-product button.pswp__button,
.single-product button.pswp__button--arrow--left::before,
.single-product button.pswp__button--arrow--right::before,
.single-product button.pswp__button:hover {
  border: none;
}
.single-product .pswp--touch .pswp__button--arrow--left,
.single-product .pswp--touch .pswp__button--arrow--right {
  visibility: visible;
}
.single-product.woocommerce .onsale {
  right: auto;
  left: 0;
  top: 10px;
}

.related-product-title-row {
  padding-block-start: 208px;
}
@media screen and (max-width: 1199px) {
  .related-product-title-row {
    padding-block-start: 80px;
  }
}
@media screen and (max-width: 767px) {
  .related-product-title-row {
    padding-block-start: 40px;
  }
}

section.related.products,
.product-showcase__title-row {
  clear: both;
}

